import React, { Children, cloneElement, useRef } from 'react'
import { Swiper, SwiperSlide } from 'swiper/react'
import { Navigation, Pagination } from 'swiper'
import styled from '@emotion/styled'

import { Box } from '@chakra-ui/react'
import useMatchBreakpoints from '../../hooks/useMatchBreakpoints'
import { colors } from '../../styles'

const SliderWrapper = styled(Box)`
  .swiper {
    width: 100%;
    height: 100%;
    padding: 20px 8px;
    padding-top: 50px;
  }
  .swiper-pagination {
    display: none;
  }
  .swiper-button-disabled {
    opacity: 0.3;
  }

  .swiper-button-prev:after,
  .swiper-button-next:after {
    font-size: 14px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    top: 25px;
    border-radius: 12px;
    padding: 0px 10px;
    height: 30px;
    color: ${colors.primary};
  }
  .swiper-button-prev {
    right: 44px;
    left: auto;
  }
  .swiper-slide {
    ${(p) => p.theme.breakpoints.sm} {
      width: min(calc(calc(100% / ${(p) => p.slidesperview}) - 10px), 381.333px);
      margin-right: 20px;
    }
  }

  .swiper-wrapper {
    display: flex;
  }
`

export default function NFTSlider({ ...props }) {
  const containerRef = useRef()

  const { isTablet, isDesktop, isMobile } = useMatchBreakpoints()

  return (
    <SliderWrapper
      ref={containerRef}
      slidesperview={isMobile ? props.mobileSlidesPerView : isTablet ? 2 : props.desktopSlidesPerView}
      className="slider_wrapper"
    >
      <Swiper
        slidesPerView={isMobile ? props.mobileSlidesPerView : isTablet ? 3 : props.desktopSlidesPerView}
        spaceBetween={20}
        slidesPerGroup={isMobile ? props.mobileSlidesPerView : isTablet ? 2 : props.desktopSlidesPerView}
        // loop={true}
        loopFillGroupWithBlank={true}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Pagination, Navigation]}
        className="mySwiper"
      >
        {Children.map(props.children, (child, index) => {
          return <SwiperSlide key={index}>{cloneElement(child)}</SwiperSlide>
        })}
      </Swiper>
    </SliderWrapper>
  )
}
