import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { InfoOutlineIcon } from '@chakra-ui/icons'
import {
  Popover,
  PopoverTrigger,
  PopoverContent,
  PopoverBody,
  PopoverArrow,
  PopoverCloseButton,
  Box,
  Text,
} from '@chakra-ui/react'
import styled from '@emotion/styled'
import Slider from '../../components/Slider'
import CollectionCard from '../../components/CollectionCard'
import { getFeaturedCollectionById } from '../../state/collection/source'
import { updateCollectionViewOpen, updateSelectedCollectionId } from '../../state/collection/reducer'

const StyledInfoIcon = styled(InfoOutlineIcon)`
  position: relative;
  z-index: 3;
  cursor: pointer;
  margin-left: 5px;
`

const FeaturedCollections = ({ accessKey }) => {
  const [collections, setCollections] = useState([])

  const dispatch = useDispatch()

  const getCollections = useCallback(
    async (accessKey) => {
      try {
        const _collections = await getFeaturedCollectionById(accessKey)
        setCollections(_collections)
      } catch (error) {
        console.log(error)
      }
    },
    [accessKey],
  )

  useEffect(() => {
    if (accessKey) {
      getCollections(accessKey)
    }
  }, [getCollections, accessKey])

  const onClickCard = (id) => {
    dispatch(updateSelectedCollectionId({ id }))
    dispatch(updateCollectionViewOpen({ collectionViewOpen: true }))
  }

  if (!accessKey) return null
  return (
    <Box bg="background">
      <Text fontSize={'20px'} fontWeight={600} mb={'-35px'} ml={'10px'} mt={'10px'}>
        Featured Collections
        <Popover autoFocus={false} trigger="hover">
          <PopoverTrigger>
            <StyledInfoIcon boxSize={4} color="#F0AD2D" />
          </PopoverTrigger>
          <PopoverContent>
            <PopoverArrow />
            <PopoverCloseButton />
            <PopoverBody>
              <Text fontSize={14} fontWeight={500}>
                To be featured, raise a{' '}
                <a
                  target="_blank"
                  href="https://forms.clickup.com/37445485/f/13pqvd-1725/9ZWUMF97QSUXF01LBO"
                  style={{ fontWeight: 700 }}
                  rel="noreferrer"
                >
                  ticket
                </a>
                ?
              </Text>
            </PopoverBody>
          </PopoverContent>
        </Popover>
      </Text>
      {collections.length > 0 && (
        <Slider mobileSlidesPerView={1} desktopSlidesPerView={3}>
          {collections.map((collection, index) => (
            <Box cursor={'pointer'} onClick={() => onClickCard(collection.collectionId)} key={index}>
              <CollectionCard details={collection} showDescription={false} />
            </Box>
          ))}
        </Slider>
      )}
      {/* <Flex w="99%" justifyContent="flex-end" pb="4">
        <DataSource />
      </Flex> */}
    </Box>
  )
}

export default FeaturedCollections
