import React, { useState } from 'react'
import styled from '@emotion/styled'
import Countdown from 'react-countdown'
import * as dayjs from 'dayjs'

import { Box, Flex, Text } from '@chakra-ui/react'

import TokenIcon from '../Svgs/tokenIcons'
import useMatchBreakpoints from '../../hooks/useMatchBreakpoints'
import { getImageUrl } from '../../utils'

const CollectionCard = ({ details }) => {
  const [readMore, setReadMore] = useState(false)
  const { contract, profile, stats, mintEnabled } = details

  const { chainId } = contract
  const { profileImage, coverImage, name } = profile
  const { mintPercentage } = stats

  return (
    <CollectionCardWrapper className="collection-card">
      <CoverImageWrapper>
        <Timer
          launchDate={details?.stats?.startDate}
          chainId={chainId}
          mintEnabled={mintEnabled}
          mintPercentage={mintPercentage}
        />
        <img src={getImageUrl(coverImage, { height: 200, quality: 80 })} alt="cover image" />
      </CoverImageWrapper>
      <MetaDataTop>
        <ProfileImageWrapper>
          <img src={getImageUrl(profileImage, { height: 100, quality: 80 })} alt="profile image" />
        </ProfileImageWrapper>
        <Box width={'100%'}>
          <StyledText fontSize="16px" fontWeight={600}>
            {name}
          </StyledText>
          <Flex>
            <Text fontSize="12px" fontWeight={500} color="text" textAlign="left" mr={'4px'}>
              by{' '}
            </Text>
            <StyledText fontSize="12px" fontWeight={500} textAlign="left">
              @{name || 'Name'}
            </StyledText>
          </Flex>

          <CollectionDetails stats={stats} chainId={chainId} />
        </Box>
      </MetaDataTop>
    </CollectionCardWrapper>
  )
}

const Timer = ({ launchDate, chainId, mintEnabled = false, mintPercentage }) => {
  const [timePassed, setTimePassed] = useState(false)
  const onComplete = () => {
    setTimePassed(true)
  }

  const renderer = ({ formatted }) => {
    return (
      <StyledText fontSize={12} fontWeight={600} color="white;">
        Starts in - {formatted.days}D : {formatted.hours}H : {formatted.minutes}M : {formatted.seconds}S
      </StyledText>
    )
  }

  const hasStarted = dayjs.unix(Number(launchDate)).isBefore(dayjs())

  const hideTimer = timePassed || !launchDate || hasStarted

  const soldOut = mintPercentage >= 100

  return (
    <TimerWrapper>
      <Box mr="auto">
        {!soldOut && mintEnabled && hasStarted ? (
          <Text fontSize={12} fontWeight={600} color="white">
            Mint Now
          </Text>
        ) : (
          <Text fontSize={12} fontWeight={600} color="white">
            {soldOut ? 'Sold Out' : hasStarted ? 'Mint Live' : ''}
          </Text>
        )}
        {!hasStarted && !hideTimer && (
          <Countdown date={new Date(Number(launchDate) * 1000)} renderer={renderer} onComplete={onComplete} />
        )}
      </Box>
      <Chain>
        <TokenIcon width={16} height={16} chainId={chainId} />
      </Chain>
    </TimerWrapper>
  )
}

const CollectionDetails = ({ stats, chainId }) => {
  const { isMobile } = useMatchBreakpoints()
  const { mintPercentage, maxSupply, price } = stats

  return (
    <Flex justifyContent="space-between" mt={'10px'}>
      <StatsBox
        mr="5px"
        value={`${mintPercentage}%`}
        title="Mint %"
        tokenIcon={false}
        showValue={!!mintPercentage}
        chainId={chainId}
      />
      {!isMobile && <StatsBox mr="5px" value={maxSupply} title="Total Supply" tokenIcon={false} chainId={chainId} />}
      <StatsBox
        mr="0px"
        value={price.value}
        title="Mint Price"
        tokenIcon={true}
        showValue={!!price?.value}
        chainId={chainId}
      />
    </Flex>
  )
}

const StatsBox = ({ title, value, tokenIcon = false, showValue = true, mr, chainId }) => {
  return (
    <Box mr={mr}>
      <Box>
        <Text fontSize={12} fontWeight={400} lineHeight="15px" color="text" mb={'5px'}>
          {title}
        </Text>
        <Flex alignItems="center">
          {showValue ? (
            <>
              {tokenIcon && <TokenIcon width={12} height={12} chainId={chainId} />}
              <Text fontSize={12} fontWeight={700} lineHeight="15px" textAlign={'center'} ml={'2px'}>
                {value || 0}
              </Text>
            </>
          ) : (
            <Text lineHeight="18px">--</Text>
          )}
        </Flex>
      </Box>
    </Box>
  )
}

const CollectionCardWrapper = styled(Box)`
  background-color: ${(p) => p.theme.colors.background};
  border: 1px solid transparent;
  border-color: ${(p) => p.theme.colors.cardBorder};
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.12);
  border-radius: 12px;
  overflow: hidden;
  padding: 10px;
  position: relative;

  &:hover {
    background-color: ${(p) => p.theme.colors.background};
    border-color: ${(p) => p.theme.colors.foreground};
  }
`
const CoverImageWrapper = styled(Box)`
  width: 100%;
  height: 120px;
  position: relative;
  border-radius: 10.96px 10.96px 0px 0px;
  overflow: hidden;
  background-color: ${({ theme }) => theme.colors.backgroundAlt};

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
`

const MetaDataTop = styled(Flex)`
  grid-gap: 14px;
  margin-top: 14px;
`

const ProfileImageWrapper = styled(Box)`
  min-width: 100px;
  width: 95px;
  height: 95px;
  min-width: 95px !important;
  background-color: ${({ theme }) => theme.colors.backgroundAlt};

  border-radius: 12%;
  overflow: hidden;
  position: relative;
  z-index: 1;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
`

const StyledText = styled(Text)`
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  white-space: normal;
  word-break: break-all;
  overflow: hidden;
  cursor: pointer;
`

const TimerWrapper = styled(Flex)`
  position: absolute;
  bottom: 0;
  z-index: 2;
  border-radius: 0;
  padding: 5px 10px;
  width: 100%;
  justify-content: space-between;
  align-items: center;

  -webkit-backdrop-filter: blur(46px);
  backdrop-filter: blur(46px);
  background: rgba(0, 0, 0, 0.2);
`
const Chain = styled(Box)`
  background-color: #ffffff;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
`

export default CollectionCard
